'use strict';
const Actions = require('../actions');
const Button = require('../../../../components/form/button.jsx');
const ControlGroup = require('../../../../components/form/control-group.jsx');
const React = require('react');
const ReactHelmet = require('react-helmet');
const ReactRouter = require('react-router-dom');
const Spinner = require('../../../../components/form/spinner.jsx');
const Store = require('./store');
const TextControl = require('../../../../components/form/text-control.jsx');


const Helmet = ReactHelmet.Helmet;
const Link = ReactRouter.Link;


class LoginPage extends React.Component {
    constructor(props) {

        super(props);

        Actions.getUserCreds();

        this.input = {};
        this.state = Store.getState();
    }

    componentDidMount() {

        this.unsubscribeStore = Store.subscribe(this.onStoreChange.bind(this));

        if (this.input.username) {
            this.input.username.focus();
        }
    }

    componentWillUnmount() {

        this.unsubscribeStore();
    }

    onStoreChange() {

        this.setState(Store.getState());
    }

    handleSubmit(event) {

        event.preventDefault();
        event.stopPropagation();

        Actions.login({
            username: this.input.username.value(),
            password: this.input.password.value()
        });
    }

    render() {

        const alerts = [];

        if (this.state.success) {
            alerts.push(<div key="success" className="alert alert-success">
                Success. Redirecting...
            </div>);
        }

        if (this.state.error) {
            alerts.push(<div key="danger" className="alert alert-danger">
                {this.state.error}
            </div>);
        }

        let formElements;

        if (!this.state.success) {
            formElements = <fieldset>
                <TextControl
                    ref={(c) => (this.input.username = c)}
                    name="username"
                    placeholder="Email"
                    hasError={this.state.hasError.username}
                    help={this.state.help.username}
                    disabled={this.state.loading}
                />
                <TextControl
                    ref={(c) => (this.input.password = c)}
                    name="password"
                    type="password"
                    placeholder="Password"
                    hasError={this.state.hasError.password}
                    help={this.state.help.password}
                    disabled={this.state.loading}
                />
                <ControlGroup hideLabel={true} hideHelp={true}>
                    <Button
                        type="submit"
                        inputClasses={{ 'btn-primary full-width mt20': true }}
                        disabled={this.state.loading}>

                        Sign in
                    </Button>
                    
                </ControlGroup>
                
            </fieldset>;
        }

        return (
            <section>
                <Helmet>
                    <title>Sign in</title>
                </Helmet>
                <div className="row wrapper border-bottom white-bg page-heading">
                    <div className="container">
                        <div className="col-lg-10">
                            <h2>Sign In</h2>
                        </div>
                        <div className="col-lg-2">
                        </div>
                    </div>
                </div>
                <div className="wrapper wrapper-content text-center animated fadeInRight">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-4 col-lg-offset-4">
                                <form onSubmit={this.handleSubmit.bind(this)}>
                                    {alerts}
                                    {formElements}
                                </form>
                        
                                <Link className="mb10" to="/login/forgot"><small>Forgot your password?</small></Link>
                                <p className="text-muted text-center"><small>Do not have an account?</small></p>
                                <Link className="btn btn-sm btn-white btn-block" to="/signup">Register New Account</Link>
                            </div>
                        </div>
                    </div>
                </div>

            </section>
        );
    }
}


module.exports = LoginPage;



